@import url('https://fonts.googleapis.com/css?family=Lato:100&display=swap');

.containerBottonAnimate {
  display: flex;
  justify-content: center;
  align-items: start;
  
}
.center {
  width: 180px;
  height: 60px;
  position: relative;
}
.center a{
  text-decoration: none;
  background-color: transparent;
}

.btnLinkAnimate {
  width: 180px;
  height: 60px;
  cursor: pointer;
  background:rgba(0, 0, 0, 0.8);
  border:none;
  outline: none;
  transition: 1s ease-in-out;
}

.svgBtnAnimate {
  position: absolute;
  left: 0;
  top: 0;
  fill: none;
  stroke: #fff;
  stroke-dasharray: 150 480;
  stroke-dashoffset: 150;
  transition: 1s ease-in-out;
}

.btnLinkAnimate:hover {
  transition: 1s ease-in-out;
  background: rgba(0, 0, 0, 0.5);
}

.btnLinkAnimate:hover .svgBtnAnimate {
  stroke-dashoffset: -480;
}

.btnLinkAnimate span {
  color: white;
  font-size: 18px;
  font-weight: 500;
  width: auto;

}
.btnLinkAnimate span:hover {
    transition: 1s ease-in-out;
    color: black;

  }
  